.icon {
  display: inline-block;
  width: 0.9em;
  height: 0.9em;
  overflow: visible;

  &.mod-small {
    width: 0.7em;
    height: 0.7em;
  }

  &.mod-normal {
    width: 1em;
    height: 1em;
  }

  &.mod-align-with-text {
    line-height: 16px;
    vertical-align: -2px;
  }

  @for $i from 5 through 15 {
    $size: $i * 2;
    &.mod-#{$size} {
      width: $size + px;
      height: $size + px;

      @extend .mod-align-with-text;
    }
  }

  /* makes the icon 33% larger relative to the icon container */
  &.mod-lg {
    width: (4em / 3);
    height: (4em / 3);

    line-height: (4em / 3);
    vertical-align: -0.1em;
  }

  &.mod-2x {
    width: 2em;
    height: 2em;

    vertical-align: -0.5em;
  }

  &.mod-3x {
    width: 3em;
    height: 3em;

    vertical-align: -1em;
  }

  &.mod-4x {
    width: 4em;
    height: 4em;

    vertical-align: -1.5em;
  }

  &.mod-5x {
    width: 5em;
    height: 5em;

    vertical-align: -2em;
  }
}

.icon-container {
  display: inline-block;
}
