.badge {
  box-sizing: border-box;
  display: inline-block;
  height: $badge-height;
  padding: $badge-padding;
  margin-right: $badge-margin;

  color: $pure-white;
  font-size: $badge-font-size;
  line-height: $badge-height;

  background-color: $orange;

  &:last-child {
    margin-right: 0;
  }
}
