h1, h2, h3, h4, h5, h6 {
  font-family: $base-font-family;
  line-height: $header-line-height;
}

h1, .h1 {
  font-size: $big-title-font-size;
}

h2, .h2 {
  font-size: 20px;
}

h3, .h3 {
  font-size: $default-font-size;
}

h4, .h4 {
  font-size: 13px;
}

h5 {
  font-size: 13px;
}

h6 {
  font-size: 13px;
}

.mod-header-min-height {
  min-height: $header-line-height;
}
